import {Component, OnInit} from '@angular/core';
import {NavService} from "../Services/Nav.service";
import {ActivatedRoute} from "@angular/router";
import {SuperData} from "../Data/super-data";
import {SuperDataManagementService} from "../Services/super-data-management.service";


@Component({
  selector: 'app-super-item-container',
  templateUrl: './super-item-container.component.html',
  styleUrls: ['./super-item-container.component.css']
})
export class SuperItemContainerComponent implements OnInit {
  superData: SuperData;
  currentItemId: string;

  constructor(private navService: NavService, private actRoute: ActivatedRoute,
              private dataManagService: SuperDataManagementService) {
  }

  ngOnInit() {
    this.dataManagService.supers.subscribe(supers => {
      this.superData = supers.find((sup) => sup.id === Number(this.currentItemId));
    });

    this.actRoute.params.subscribe(routeParams => {
      this.currentItemId = routeParams.id;
      this.superData = this.dataManagService.getCurrentSupers().
                        find(sup => sup.id === Number(this.currentItemId));
    });

    this.InitNavStatus();
  }

  private InitNavStatus() {
    const navstatus: navStatus = {  // Update the navigation bar
      _topNav: {
        addBtn: false,
        searchBtn: false,
        editBtn: true,
        deleteBtn: true
      },
      _bottomNav:{
        showNav: true,
        showTopBtn: false
      }
    }
    this.navService.setNavStatus(navstatus);
  }
}
