import { Component, OnInit } from '@angular/core';
import {SuperData} from '../Data/super-data';
import {SuperDataManagementService} from '../Services/super-data-management.service';

@Component({
  selector: 'app-links',
  templateUrl: './links.component.html',
  styleUrls: ['./links.component.css']
})
export class LinksComponent implements OnInit {

  listOfSuper: SuperData[];

  constructor(private dataManagService: SuperDataManagementService) {
  }

  ngOnInit() {
    this.dataManagService.supers.subscribe(supers => {
      console.log('links comp update')
      this.listOfSuper = supers; });
  }
}
