import {Injectable} from '@angular/core';
import {SuperData} from '../Data/super-data';
import {NavService} from './Nav.service';
import {HttpService} from './http.service';
import {BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SuperDataManagementService {

  private _supers: BehaviorSubject<SuperData[]>;

  constructor(private messageService: NavService, private httpService: HttpService) {
    this._supers = <BehaviorSubject<SuperData[]>>new BehaviorSubject(null);
    this.loadInitialData();
  }

  get supers() {
    return this._supers.asObservable();
  }

  getCurrentSupers(){
    return this._supers.getValue();
  }

  loadInitialData() {
    this.httpService.getSuperList()
      .subscribe(
        supers => this._supers.next(supers), err => console.log('Error retrieving Todos'));
  }

  add(superData: SuperData): void {
    this.httpService.add(superData).subscribe(sup => {
      const newSupers: SuperData[] = this._supers.getValue();
      newSupers.push(sup);
      console.log(sup);
      this._supers.next(newSupers);
    });
  }

  Update(superData: SuperData, Id: Number): void  {
    this.httpService.Update(superData, Id).subscribe(sup => {
      const newSupers: SuperData[] = this._supers.getValue();
      const index = newSupers.findIndex((s) => s.id === Id);
      newSupers[index].numOfEmployees = sup.numOfEmployees;
      newSupers[index].brand = sup.brand;
      newSupers[index].owner = sup.owner;
      newSupers[index].info = sup.info;
      newSupers[index].size = sup.size;
      this._supers.next(newSupers);
    });
  }

  DeleteSuper(Id: Number) {
    this.httpService.DeleteSuper(Id).subscribe(sup => {
      const newSupers: SuperData[] = this._supers.getValue();
      const index = newSupers.findIndex((s) => s.id === Number(Id));
      newSupers.splice(index, 1);
      this._supers.next(newSupers);
    });
  }

}
