import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {SuperData} from '../Data/super-data';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  constructor(private http: HttpClient) { }

  add(superData: SuperData): Observable<SuperData> {
    return this.http.post<SuperData>('api/super-list', superData);
  }

  Update(superData: SuperData, Id: Number): Observable<SuperData> {
    return this.http.put<SuperData>(`api/super-list/${Id}`, superData);
  }

  getSuperList(): Observable<SuperData[]> {
    return this.http.get<SuperData[]>('api/super-list');
  }

  DeleteSuper(Id: Number): Observable<SuperData> {
    return this.http.delete<SuperData>(`api/super-list/${Id}`);
  }
}
