import { Component, OnInit } from '@angular/core';
import {SuperData} from '../Data/super-data';
import {NavService} from '../Services/Nav.service';
import {ActivatedRoute} from '@angular/router';
import {SuperDataManagementService} from '../Services/super-data-management.service';


@Component({
  selector: 'app-search-results',
  templateUrl: './search-results.component.html',
  styleUrls: ['./search-results.component.css']
})
export class SearchResultsComponent implements OnInit {

  filteredSupers: SuperData[];
  noResults: boolean;
  constructor(private messageService: NavService, private actRoute: ActivatedRoute,
              private dataManagService: SuperDataManagementService) { }

  ngOnInit() {
    this.actRoute.queryParams.subscribe(routeParams => {
      this.dataManagService.supers.subscribe((supers) => {
        this.filteredSupers = this.getFilteredList((routeParams.For).toLowerCase(), supers);
        if (this.filteredSupers.length > 0) {
          this.noResults = false;
          this.InitNavStatus();
        }
        else {
          this.noResults = true;
        }
      });
      }
    );
  }

  private InitNavStatus() {
    const navstatus: navStatus = {  // Update the navigation bar
      _topNav: {
        addBtn: false,
        searchBtn: true,
        editBtn: false,
        deleteBtn: false
      },
      _bottomNav:{
        showNav: true,
        showTopBtn: true
      }
    }
    this.messageService.setNavStatus(navstatus);
  }

  getFilteredList(search: string , superArray: SuperData[]) {
    return superArray.filter(rec => rec.size.toLowerCase().includes(search) ||
      rec.info.toLowerCase().includes(search) ||
      rec.owner.toLowerCase().includes(search) || rec.brand.toLowerCase().includes(search));
  }
}
